import { plural } from '@/helpers/helpers';

const format = require('format-number');

const errors = {
  required: '  ',
  minLength: '    param_value',
  maxLength: '    param_value',
  minValue: '     param_value',
  maxValue: '     param_value',
  startStr: '   param_value',
  equality: '   param_value',
};

const getErrors = function (field_name, v) {
  const flattenParams = v.$flattenParams();
  const errors_array  = [];
  flattenParams.forEach((param) => {
    let output_str = errors[param.name];
    if ( ! v[param.name]) {
      if (output_str) {
        if ('max' in param.params) {
          switch (param.name) {
            case 'maxLength':
              output_str = output_str.replace(
                  'param_value',
                  `${format({ integerSeparator: ' ', decimal: ',' })(param.params.max)} ${plural(param.params.max, ['', '', ''])}`,
              );
              break;
            case 'maxValue':
              output_str = output_str.replace('param_value', format({ integerSeparator: ' ', decimal: ',' })(param.params.max));
              break;
          }
        }
        if ('min' in param.params) {
          switch (param.name) {
            case 'minLength':
              output_str = output_str.replace(
                  'param_value',
                  `${format({ integerSeparator: ' ', decimal: ',' })(param.params.min)} ${plural(param.params.min, ['', '', ''])}`,
              );
              break;
            case 'minValue':
              output_str = output_str.replace('param_value', format({ integerSeparator: ' ', decimal: ',' })(param.params.min));
              break;
          }
        }
        if ('start' in param.params) {
          output_str = output_str.replace('param_value', param.params.start);
        }
        if ('length' in param.params) {
          output_str = output_str.replace(
              'param_value',
              `${format({ integerSeparator: ' ', decimal: ',' })(param.params.length)} ${plural(param.params.length, ['', '', ''])}`,
          );
        }
        errors_array.push(output_str);
      }
    }
  });
  return errors_array;
};

export default getErrors;
